import uniPopup from '@/components/uni-popup/uni-popup.vue'
import amount from "@/components/shopping/amount.vue"
export default {
	data() {
		return {
			type: '',
			colorIndex:0,
			containIndex:0,
			suitIndex:0
		};
	},
	components: {
		uniPopup,
		amount
	},
	props:["arr"],
	watch:{
		arr(newarr){
			this.showUp()
		}
	},
	methods:{
		showUp(){
			this.type = 'bottom'
			this.$refs['popup'].open()
		}
	}
}
